@import "../../themes/theme.scss";

#skills{
  position: static;
  padding-top: 5em;
  background: url('./../../assets/img/background/skills-background.png');
  background-repeat: no-repeat;
  background-size: cover;

  p {
    font-size: 1.1em;
    color: $background;
  }
}

#skills-box{
  align-self: center;
  margin: 0;
  padding-bottom: 10em;
}

.skill-column{
  align-self: center;

}

.skill-image{
  width: 6em;
  margin: 2vw;
  padding-top: 3em;
  padding-bottom: 1.5em;

  @media only screen and (max-width: 599px){
    width: 4em;
    margin: 1vw;
  }
}

.skill-computer-image{
  width: 100%;
  background: transparent;
}

.skill-row{
  justify-content: center;
}
